html {
  height: 100%;
  box-sizing: border-box;
}
body {
  margin: auto;
  min-height: 100%;
  position: relative;
  font-family: sans-serif;
  width: 640px;
  background-color: whitesmoke;
  background-image: url(/static/media/circuit.f7b85df6.svg);
}
p {
  color: rgb(100 100 100);
}
.header {
  border-radius: 4px;
  line-height: 1em;
  background-color: #f9f9f9;
  border: 0.5px solid #E7E9EA;
}
.page-content {
  position: relative;
  margin: 0 auto;
  height: 100%;
  min-height: 100%;
}
.content {
  padding: 20px;
  background-color: white;
  border-radius: 4px;
  margin-bottom: 40px;
  border: 0.5px solid #E7E9EA;
}
.footer {
  font-size: 0.8em;
  width: 100%;
  position: absolute;
  right: 0;
  bottom: 0;
  left: 0;
  padding: 1rem;
}
ul.header li {
  display: inline;
  list-style-type: none;
  margin: 0;
  padding: 20px;
}
ul.header {
  padding: 0;
}
ul.header li a {
  font-weight: bold;
  text-decoration: none;
  padding: 20px;
  display: inline-block;
  color: black;
}
h2 {
  padding: 0;
  margin: 0;
  font-size: 16px;
  text-decoration: none;
  display: inline;
}
.active {
  border-bottom: 2px solid #b3a8ec;
}
.present {
  margin: 0 0 20px 0;
  padding: 0;
  font-size: 16px;
  color: black;
}
.present-block {
  display: inline-block;
  width: 415px;
  vertical-align: top;
}
.present-block-img {
  display: inline-block;
  border-radius: 3px;
  margin: 0px 15px 0px 15px;
  padding: 0;
}
.contact {
  margin: 20px 0 20px 0;
  padding: 0;
}
.contact a {
  color: rgb(100 100 100);
}
.contact-icon {
  display: inline-block;
  margin: 0;
  color: rgb(100 100 100);
}
.contact-icon a {
  color: rgb(100 100 100);
}
.head-works {
  color: black;
}

